.host {
    padding: 10px;
    justify-content: space-evenly;
    display: inline-flex;
    flex-wrap: wrap;
    align-content: center;
    p {
        font-family: var(--app-text-main-font);  
    }
}

.header {
    justify-content: space-between;
    display: flex;
    
}
.closeContainer {
    position: absolute;
    top: -19px;
    right: 19px;
}

.closeElement {
    cursor: pointer;
    background-color: transparent;
    border: 0;
}

.closeIcon {
    width: 48px;
    height: 48px;
}

.target {
    //flex: 1;
}

.title {
    text-align: center;
    font-size: 2em;
    color: var(--app-color-gray-dark);
    width: 100%;
    word-break: break-word;
    text-transform: capitalize;
}

.subtitle {
    margin-top: -20px;
    margin-bottom: 40px;
    text-align: center;
    font-size: 16px;
    color: var(--app-color-gray-dark);
    width: 100%;
    
}


.controls {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 30px;
    padding-bottom: 10px;
    position: relative;
}


.errorContainer {
    font-family: var(--app-text-main-font);
    font-size: 12px;
    color: var(--app-color-red-error);
    white-space: pre;
    word-break: break-all;
}

.messageContainer {
    font-family: var(--app-text-main-font);
    font-size: 12px;
    color: gray;
    white-space: pre;
    word-break: break-all;
}

  
  .errorParagraph {
    margin: 10px 0 0 0;
    text-align: left;
    vertical-align: center;
    color: red;
    font-family: var(--app-text-main-font);
    font-size: 0.8rem;
    font-weight: 400;
    letter-spacing: 0;
    line-height: 0.8rem;
  }
  
  
  .trialContainer {
    
    background-color: #FFFFFF;
    border-radius: 4px;
    
  }
  
  .trialTitle {
    font-weight: bolder;
    font-size: 20px;
    color: gray;
    text-align: left;
    font-family: var(--app-text-main-font), serif;
  }
  
  
  .subTitle {
    margin: 0 0 8px 0px;
    font-size: 14px;
    color: gray;
    text-align: left;
    font-family: var(--app-text-main-font), serif;
  }
  .trialText {
    margin-top: auto;
    margin-bottom: auto;
    margin-right: 10px;
    color: black;
    font-weight: normal;
    font-size: 24px;
  }
  
  .trialButton {
    font-family: var(--app-text-main-font), serif;
    border-radius: 4px;
    color: #FFFFFF;
    font-size: 16px;
    margin-left: 3px;
    height: 32px;
  }
  
  
  .plansContainer {
    margin-bottom: 24px;
    display: flex;
  }
  
  .additionalPlanContainer {
    width: 400px;
    border-radius: 8px;
    text-align: center;
    background-color: white;
  }
  
  .planTitle {
    text-align: left;
    margin-left: 35px;
    margin-bottom: 0;
    font-family: var(--app-text-main-font);
    font-size: 24px;
    color: gray;
    font-weight: 600;
  }
  
  .planDescriptionContainer {
    display: flex;
    margin-left: 35px;
  
    img {
      align-self: baseline;
      margin-top: 20px;
      margin-right: 10px;
    }
  }
  
  .planPrice {
    text-align: left;
    margin-left: 35px;
    margin-top: 10px;
    margin-bottom: 0;
    font-weight: 200;
    font-family: var(--app-text-main-font);
    font-size: 20px;
    color: gray;
  }
  
  .planDescription {
    font-family: var(--app-text-main-font), serif;
    color: gray;
    font-size: 16px;
    text-align: left;
  }
  
  .planButton {
    width: 85%;
    border: 1px solid var(--app-color-gray-dark);
    color: var(--app-color-gray-dark);
    border-radius: 4px;
    background-color: var(--app-color-white-lightest);
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 4px;
  }
  
  .month {
    font-family: var(--app-text-main-font), serif;
    font-size: 16px;
  }
  
  .currentInvoicesContainer {
    margin-top: 30px;
    margin-bottom: 40px;
    background-color: white;
    border: 0px dashed var(--app-color-gray-dark);
    margin-right: 24px;
    float: left;
    border-radius: 8px;
    padding: 0 20px 20px 20px;
    border-radius: 8px;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
    
  }
  
  .logoutButton {
    border: 1px solid var(--app-color-error);
    background-color: transparent;
    border-radius: 4px;
    color: var(--app-color-error);
    width: 200px;
  }
  
  .boxesHost {
    margin: 10px;
    width: 200px;
  
    .products {
      text-align: center;
      font-size: 24px;
      font-family: var(--app-text-main-font), serif;
      margin-left: auto;
      margin-right: auto;
      font-weight: 300;
      padding-top: 20px;
    }
  
    .checkoutRightContainer {
      margin-top: 50px;
  
      .productsContainer {
        box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
        min-height: 460px;
      }
  
      .servicesContainer {
        .hr {
          border-top: 1px solid #D2D2D2;
        }
  
        padding-bottom: 10px;
        background: var(--app-color-gray-lighter);
        border-radius: 8px;
      } 
    }
    .endingMessage {
      padding: 0px 24px 24px 24px;
      font-size: 12px;
    
      font-family: var(--app-text-main-font), serif;
     
    }
  }
  
  .userPanelHost {
    display: flex;
    flex-direction: column;
    background-color: rgb(246,244,236);
    border-radius: 7px;
    padding: 20px 0px 20px 0px;
  }
  
  .userPanelRows {
    display: flex;
    flex-direction: column;
    background-color: rgb(246,244,236);
    border-radius: 7px;
    padding: 0px 20px 00px 20px;
  }
  
  
  